export default {
  addOneTab({commit},tab){
    //job{}addOne_Mutamutations,mutations
    commit('ADD_ONE_TAB',{tab});
  },
  removeOneTab({commit},tab){
    //job{}addOne_Mutamutations,mutations
    commit('REMOVE_ONE_TAB',{tab});
  },
  initLeftTabs({commit},tabs){
    commit('INIT_LEFT_TABS',{tabs});
  }
}



